<template>
  <div class="complete">
    <div class="complete__container">
      <h2 class="complete__title">{{ congra.substring(0, textleng) }}</h2>
      <div :class="textleng >= leng ? 'complete__img show' : 'complete__img'">
        <img :src="require('@/assets/pics/' + scrResult + '.jpg')" ref="photo" />
      </div>
      <p :class="textleng >= leng ? ' show' : ''" class="complete__tips" @click="downloadImg">長按圖片儲存</p>
      <router-link :class="textleng >= leng ? ' show' : ''" class="complete__start" to="/">RESTART</router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import html2canvas from 'html2canvas'
export default {
  name: 'Complete',
  data() {
    return {
      textleng: 0,
      leng: 0,
      congra: 'Congrats! This gift is for you.',
      scrResult: '1'
    }
  },
  mounted() {
    let result = this.answersGetter.join('')
    switch (result) {
      case '000':
        this.congra = '經歷翻山越嶺後的艱辛，終於能有一口的喘息，停頓休息，再往下個方向前行吧。攝於玉長公路'
        this.scrResult = 1
        break
      case '001':
        this.congra = '豐收的滿足與喜悅，需要多少的陽光雨水和辛勞，金黃色的海洋，在山間縱谷中流動著。攝於池上稻穗間'
        this.scrResult = 2
        break
      case '010':
        this.congra = '一個人的早晨，刻意把時間留給自己，與城市對話，重新出發吧！攝於台南美術館旁'
        this.scrResult = 3
        break
      case '011':
        this.congra = '享受當下的感動與幸運，在屬於我的位置和時間線上。攝於衛武營'
        this.scrResult = 4
        break
      case '100':
        this.congra = '時間像是靜止在此地，乘載著多少人來人往，以書會友的時代，是多麼珍貴。攝於關西老街石店子69有機書店'
        this.scrResult = 5
        break
      case '101':
        this.congra = '隨心所欲，只願在此刻找到那一個給我平靜的瞬間。攝於屏東竹田車站旁'
        this.scrResult = 6
        break
      case '110':
        this.congra = '如誇父逐日，與夕陽追逐的旅途，記錄下我與你的最靠近的一刻。攝於台南後壁'
        this.scrResult = 7
        break
      case '111':
        this.congra = '旅途上隻身一人的自己，不是孤單的，那堅強的背影，激勵著自己旅途還沒結束攝於台九線上'
        this.scrResult = 8
        break
      default:
        this.congra = '經歷翻山越嶺後的艱辛，終於能有一口的喘息，停頓休息，再往下個方向前行吧。攝於玉長公路'
        this.scrResult = 1
        break
    }
    this.leng = this.congra.length
    for (let i = 0; i < this.leng; i++) {
      setTimeout(() => {
        this.textleng++
      }, i * 200)
    }
  },
  computed: {
    ...mapGetters(['answersGetter'])
  },
  methods: {
    downloadImg() {
      html2canvas(this.$refs.photo, {
        backgroundColor: null, // 背景颜色
        dpi: 192, // 提高分辨率,默認值为96
        scale: 2, // 渲染的比例尺
        useCORS: true // 使用CORS下載圖片
      }).then((canvas) => {
        var dataUrl = canvas.toDataURL()
        var newImg = document.createElement('a')
        newImg.href = dataUrl
        newImg.setAttribute('download', 'download.png')
        document.querySelector('body').append(newImg)
        newImg.click()
        newImg.remove()
      })
    }
  }
}
</script>
